.my-tab-panel {
	.components-tab-panel__tabs {
		display: flex;
		justify-content: space-between;
		margin-bottom: 12px;
	}

	.components-tab-panel__tabs-item {
		display: flex;
		justify-content: center;
		justify-items: center;
		background: none;
		width: 100%;
		border: none;
		padding: 8px 0;
		font-size: 14px;
		cursor: pointer;
		color: #666;
		position: relative;
		border-bottom: 2px solid #ddd;

		span {
			display: flex;
			gap: 8px;
			justify-content: center;
			justify-items: center;
		}
	}

	.components-tab-panel__tabs-item:hover {
		color: #6f22dd;
	}

	.components-tab-panel__tabs-item.active-tab {
		color: #6f22dd;

		&::after {
			content: "";
			position: absolute;
			left: 0;
			bottom: -1.5px;
			width: 100%;
			height: 2px;
			border-bottom: 2px solid #6f22dd;
		}
	}
}

.layout-grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 7px;
}

.layout-card {
	cursor: pointer;

	svg {
		color: #9ea9b2;
	}

	&.is-active {
		svg {
			color: #7c3aed;
		}
	}
}

.alignment-buttons {
	display: inline-flex;
	border: 1px solid #ddd;
	border-radius: 4px;
	overflow: hidden;
	width: 100%;
	justify-content: space-around;
	padding: 4px;
}

.alignment-buttons button {
	background: #fff;
	border: none;
	padding: 6px 10px;
	width: 100%;
	cursor: pointer;

	span {
		color: #757575;
	}
}

.alignment-buttons button.is-active {
	background: #efe6fb;
	border: 1px solid #d6c2f5;

	span {
		color: #6f22dd;
	}
}

.alignment-buttons button:hover {
	background: #efe6fb;
}

.style-hover-buttons {
	display: inline-flex;
	border: 1px solid #ddd;
	border-radius: 4px;
	font-weight: 400px;
	line-height: 20px;
	overflow: hidden;
	width: 100%;
	justify-content: space-around;
	padding: 4px;

	button {
		background: #fff;
		border: none;
		padding: 8px 10px;
		width: 100%;
		cursor: pointer;
	}
}

.style-border-buttons {
	display: inline-flex;
	border: 1px solid #ddd;
	border-radius: 4px;
	font-weight: 400px;
	line-height: 20px;
	overflow: hidden;
	width: 100%;
	justify-content: space-around;
	padding: 3px;

	button {
		background: #fff;
		border: none;
		padding: 2px 0;
		width: 48px;
		height: 32px;
		cursor: pointer;
	}

	button.is-active {
		background: #efe6fb;
		border: 1px solid #d6c2f5;
		color: #6f22dd;
		border-radius: 2px;
	}
}

.style-hover-buttons button.is-active {
	background: #efe6fb;
	border: 1px solid #d6c2f5;
	color: #6f22dd;
	border-radius: 2px;
}

.style-normal-background-types-button {
	display: flex;
	justify-content: space-between;
	padding: 12px 0;

	button {
		border: none;
		display: inline-flex;
		place-content: center;
		padding: 4px !important;
		background-color: #fff;
	}

	button.is-active {
		border-radius: 2px;
		border: 1px solid var(--Primary-2-400, #884ae3);
	}
}

.color-picker-container {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;

	span {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 8px;
	}
}

.components-range-control__root {
	align-items: start !important;
}

.border-width-heading-container {
	display: flex;
	margin: 16px 0;
	justify-content: space-between;
	align-items: center;

	p {
		padding: 0;
		margin: 0;
	}

	div {
		display: flex;
		justify-content: center;
		align-items: start;
		gap: 8px;
	}
}

.image-upload-box {
	position: relative;
	height: 140px;
	margin-top: 12px;
	border: 1px solid #d6c2f5;
	border-radius: 2px;
	box-sizing: border-box;
	margin-bottom: 16px;

	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	background: #efe6fb;

	button {
		all: unset;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.image-preview {
		width: 100%;
		height: 100%;
		position: relative;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			display: block;
			cursor: pointer;
		}
	}

	.image-actions {
		position: absolute;
		inset: 0;
		display: flex;
		align-items: end;
		justify-content: center;
		gap: 10px;
	}

	.change-image {
		background: #f0f0f0;
		color: #1e1e1e;
		border: none;
		padding: 4px 8px;
		font-size: 13px;
		width: 100%;
		cursor: pointer;
	}

	.clear-image {
		position: absolute;
		top: 4px;
		right: 4px;
		width: 22px;
		height: 22px;
		border-radius: 50%;
		border: none;
		background: #f0f0f0;
		color: #1e1e1e;
		font-size: 18px;
		font-weight: 600;
		line-height: 1;
		cursor: pointer;

		&:hover {
			background: #cc1818;
		}
	}
}

.components-base-control {
	margin-bottom: 0;
}

.components-base-control,
.components-range-control,
.-bb-b-bc-cbdb-qy3gpb,
.ej5x27r4 {
	margin-bottom: 0 !important;
}

.def--bb-cd-eebada-7gj15f {
	margin-bottom: 0 !important;
}

.sib-dashicon-picker {
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	gap: 8px;
}

.sib-dashicon-btn {
	padding: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 1px solid #ddd;

	.dashicons {
		font-size: 20px;
	}

	&.is-pressed {
		border-color: #6f22dd;
		background: rgba(111, 34, 221, 0.1);
	}
}